.mainsection
	background: url("../assets/main.png")
	width: 1920px
	height: 1126px
	
.header
	display: flex
	width: 100%
	background: lightgreen
	align-items: center
	justify-content: space-between

	&__logo
		margin-top: 56px
		position: relative
		margin-left: 375px

	&__nav
		margin-top: 56px
		
		&__link
			font-family: 'Poppins'
			font-style: normal

			&:not(:last-child)
				margin-right: 48px

	&__auth
		margin-top: 56px
		font-family: 'Poppins'
		font-style: normal

		&:not(:first-child)
			margin-right: 375px
			padding: 16px 32px
			width: 133px
			height: 60px
			border-radius: 34px
			background: #FF553E
			color: white
        
		&:not(:last-child)
			margin-right: 36px
			color: #FF553E
			width: 49px
			height: 28px

.maincaption
	position: relative
	width: 800px
	height: 180px
	font-family: 'Poppins'
	font-style: normal
	font-weight: 700
	font-size: 64px
	line-height: 141%
	text-align: center
	color: #1C0E0D
	margin-left: 560px
	margin-top: 238px

	&:before
		position: absolute
		content: ""
		display: flex
		width: 135.05px
		height: 157.10px
		background: url("../assets/clock.png")
		background-size: contain
		background-repeat: no-repeat
		left: -130px
		top: -70px
		z-index: 2